    animation: hideForm ease-in-out 500ms forwards;\r\n}\r\n\r\n/* ------------- */\r\n\r\nhtml{\r\n    height: 100%;\r\n}\r\n\r\nbody{\r\n    min-height: 100vh;\r\n    width: 100%;\r\n    background-color: var(--body-bg-color);\r\n    display: flex;\r\n    flex-direction: column;\r\n    \r\n}\r\n\r\n\r\n/* STYLES */\r\nheader{\r\n    background-color: var(--header-bg-color);\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    padding: 10px 20px;\r\n}\r\n\r\nheader > .header-logo{\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 10px;\r\n}\r\n\r\n.header-logo > h1\r\n{\r\n    text-transform: uppercase;\r\n    font-family: var(--logo-font);\r\n    color: var(--header-text-color);\r\n}\r\n\r\n\r\nmain{\r\n    width: 100%;\r\n    display: flex;\r\n    flex: 1;\r\n    position: relative;\r\n}\r\n\r\n#sidebar{\r\n    position: relative;\r\n    padding: 35px 25px 75px 25px;\r\n    background-color: var(--sidebar-bg-color);\r\n    width: 350px;\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 30px;\r\n}\r\n\r\n.todos-selections,\r\n.projects-selections\r\n{\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 20px;\r\n}\r\n\r\n.todos-selections > li.active{\r\n    background-color: var(--todo-selections-bg-color-active);\r\n}\r\n\r\n.todos-selections > li,\r\n.projects-selection-container> .projects-selections > li{\r\n    display: flex;\r\n    width: 100%;\r\n    gap: 12px;\r\n    cursor: pointer;\r\n    padding: 10px 5px; \r\n    border-radius: 5px;\r\n    align-items: center;\r\n}\r\n\r\n.todos-selections > li > p{\r\n    font-family: var(--general-font);\r\n    font-weight: 500;\r\n    margin-right: auto;\r\n}\r\n\r\n.todos-selections > li:hover{\r\n    background-color: var(--todo-selections-bg-color-hover);\r\n}\r\n\r\n.todos-counter{\r\n    width: 20px;\r\n    height: 20px;\r\n    background-color: var(--todo-selections-counter-bg-color);\r\n    border-radius: 50%;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    padding: 3px;\r\n    font-family: var(--general-font);\r\n    color: var(--white-color);\r\n    font-size: .8rem;\r\n}\r\n\r\n.projects{\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 12px;\r\n}\r\n\r\n.projects-selection-container{\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 20px;\r\n}\r\n\r\n\r\n.projects-selections > li > p{\r\n    font-family: var(--general-font);\r\n    font-weight: 400;\r\n    margin-right: auto;\r\n}\r\n\r\n.projects-selections > li.active-project\r\n{\r\n    background-color: var(--todo-selections-bg-color-active);\r\n}\r\n\r\n#addProjectBtn{\r\n    width: 100%;\r\n    height: 40px;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    gap: 12px;\r\n    font-family: var(--general-font);\r\n    background-color: var(--black-color);\r\n    border-radius: 10px;\r\n    border: none;\r\n    color: var(--white-color);\r\n    cursor: pointer;\r\n}\r\n\r\n.sidebar-title{\r\n    font-family: var(--general-font);\r\n}\r\n\r\n\r\n.deleteProjectBtn{\r\n    opacity: 0;\r\n    background-color: transparent;\r\n    border: none;\r\n    cursor: pointer;\r\n}\r\n\r\n.projects-selections > li:hover > .deleteProjectBtn{\r\n    animation: showDeleteBtn ease-in-out 500ms forwards;\r\n}\r\n\r\n\r\n#content{\r\n    padding: 20px;\r\n    width: 100%;\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 20px;\r\n}\r\n\r\n.content-title{\r\n    font-size: 2rem;\r\n    font-family: var(--general-font);\r\n    position: relative;\r\n    width: max-content;\r\n}\r\n\r\n.content-title::before\r\n{\r\n    content: \"\";\r\n    position: absolute;\r\n    height: 4px;\r\n    background-color: black;\r\n    bottom: -5px;\r\n    border-radius: 10px;\r\n    animation: barSlideIN 750ms ease-in-out forwards;\r\n}\r\n\r\n.todos-content-container{\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 20px;\r\n}\r\n\r\n.todos-content-container > li{\r\n    display: flex;\r\n    width: 100%;\r\n    overflow: hidden;\r\n    border-radius: 10px;\r\n    height: 165px;\r\n    border-top: 1px solid black;\r\n    border-right: 1px solid black;\r\n    border-left: 10px solid black;\r\n    border-bottom: 1px solid black;\r\n    box-shadow: 0px 0px 20px  0px rgba(0, 0, 0, 0.222);\r\n    padding: 10px;\r\n    align-items: flex-start;\r\n    gap: 20px;\r\n    justify-content: center;\r\n}\r\n\r\n.todos-content-container > li > input{\r\n    width: 30px;\r\n    height: 30px;\r\n    accent-color: var(--black-color);\r\n}\r\n\r\n.todos-content-container > li > .todo-information{\r\n    display: flex;\r\n    flex-direction: column;\r\n    height: 100%;\r\n    gap: 10px;\r\n    width: 100%;\r\n}\r\n\r\n.todo-information > *{\r\n    font-family: var(--general-font);\r\n\r\n}\r\n\r\n.todo-information > .todo-title{\r\n    font-size: 1.5rem;\r\n}\r\n\r\n.todo-information > .todo-description{\r\n    margin-bottom: auto;\r\n    font-weight: 400;\r\n    display: -webkit-box;\r\n    -webkit-box-orient: vertical;\r\n    overflow: hidden;\r\n    text-overflow: ellipsis;\r\n    line-clamp: 2;\r\n    -webkit-line-clamp: 2; /* Show only 3 lines */\r\n    line-height: 1.5em;\r\n    max-height: calc(1.5em * 3); /* line-height * number of lines */\r\n}\r\n\r\n\r\n.todo-control{\r\n    display: flex;\r\n    gap: 20px;\r\n}\r\n\r\n\r\n.todo-control > button{\r\n    background-color: transparent;\r\n    border: none;\r\n}\r\n\r\n.add-task-button\r\n{\r\n    background-color: white;\r\n    height: 40px;\r\n    width: 50%;\r\n    box-shadow: 5px 5px 0px 0px black;\r\n    border-radius: 10px;\r\n    align-self: center;\r\n\r\n}\r\n\r\n\r\n/* form */\r\n.add-form{\r\n    position: fixed;\r\n    top: 50%;\r\n    left: 50%;\r\n    transform: translate(-50%, -50%);\r\n    z-index: 10;\r\n    background-color: white;\r\n    width: 500px;\r\n    height: auto;\r\n    border: 5px solid black;\r\n    display: none;\r\n    opacity: 0;\r\n    flex-direction: column;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    padding: 20px;\r\n    gap: 30px;\r\n}\r\n\r\n.form-title\r\n{\r\n    padding: 10px;\r\n    font-family: var(--general-font);\r\n}\r\n\r\n.add-form > .input-container,\r\n.add-form > .form-buttons{\r\n    width: 220px;\r\n    height: 30px;\r\n}\r\n\r\n.input-container{\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: flex-start;\r\n}\r\n\r\n\r\n\r\n.input-container > input,\r\n.input-container > select{\r\n    width: 100%;\r\n    height: 100%;\r\n    padding: 5px;\r\n    border: 2px solid black;\r\n}\r\n\r\n.input-container > label{\r\n    font-weight: bold;\r\n    font-family: var(--general-font);\r\n}\r\n\r\n.add-form > #priority{\r\n    display: flex;\r\n    flex-direction: row;\r\n    align-items: center;\r\n    justify-content: space-evenly;\r\n}\r\n\r\n#priority > button{\r\n    width: 60px;\r\n    height: 100%;\r\n    font-family: var(--general-font);\r\n    font-weight: 800;\r\n    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.499);\r\n    border-radius: 10px;\r\n    color: black;\r\n}\r\n\r\n#highPriority\r\n{\r\n    background-color: red;\r\n    color: white;\r\n}\r\n\r\n#mediumPriority\r\n{\r\n    background-color: yellow;\r\n    color: white;\r\n}\r\n\r\n#lowPriority\r\n{\r\n    background-color: green;\r\n    color: white;\r\n}\r\n\r\n.form-buttons\r\n{\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: space-evenly;\r\n}\r\n\r\n.form-buttons > button{\r\n    background-color: white;\r\n    border: 3px solid black;\r\n    border-radius: 5px;\r\n    width: 100px;\r\n    height: 100%;\r\n    font-family: var(--general-font);\r\n    font-weight: bold;\r\n    transition: ease-in-out 500ms;\r\n}\r\n\r\n.form-buttons > button:hover{\r\n    background-color: rgb(76, 76, 76);\r\n    color: white;\r\n}\r\n\r\n\r\n.project-description\r\n{\r\n    font-style: italic;\r\n    font-size: 1.2rem;\r\n}\r\n\r\n.burger-menu\r\n{\r\n    display: none;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    gap: 6px;\r\n    align-items: center;\r\n    cursor: pointer;\r\n    width: 30px;\r\n    height: 60px;\r\n}\r\n\r\n.burger-menu > .line\r\n{\r\n    background-color: white;\r\n    width: 100%;\r\n    height: 3px;\r\n    flex-shrink: 0;\r\n    border-radius: 1rem;\r\n}\r\n\r\n#closeSidebar\r\n{\r\n    display: none;\r\n    width: 25px;\r\n    height: 25px;\r\n    position: absolute;\r\n    top: 4px;\r\n    right: 4px;\r\n    opacity: 0;\r\n}\r\n\r\nfooter{\r\n    width: 100%;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    padding: 10px 20px;\r\n    gap: 10px;\r\n    font-family: var(--footer-font);\r\n    color: var(--footer-font-color);\r\n    background-color: var(--footer-bg-color);\r\n}\r\n\r\n\r\n/* MEDIA QUERIES */\r\n\r\n/* 2xl */\r\n@media (max-width: 1536px) \r\n{\r\n\r\n}\r\n\r\n/* xl */\r\n@media (max-width: 1280px) \r\n{\r\n\r\n}\r\n\r\n/* lg */\r\n@media (max-width: 1024px) \r\n{\r\n\r\n}\r\n\r\n@media (max-width: 880px) \r\n{\r\n    #sidebar\r\n    {\r\n        position: fixed;\r\n        height: 100%;\r\n        z-index: 5;\r\n        transform: translateX(-650px);\r\n    }\r\n\r\n    .burger-menu\r\n    {\r\n        display: flex;\r\n    }\r\n\r\n    #closeSidebar\r\n    {\r\n        display: flex;\r\n    }\r\n\r\n}\r\n\r\n/* md */\r\n@media (max-width: 768px) \r\n{\r\n\r\n}\r\n\r\n/* sm */\r\n@media (max-width: 640px) \r\n{\r\n\r\n}\r\n\r\n/* xs */\r\n@media (max-width: 545px) \r\n{\r\n    #sidebar{\r\n        width: 100%;\r\n    }\r\n\r\n    .add-form{\r\n        width: 95%;\r\n        \r\n    }\r\n}"],"sourceRoot":""}]);
